.side__bar {
  top: 80px;
  position: sticky;
  height: fit-content;

  flex: 0.18;
  border-radius: 10px;
  text-align: center;
  min-width: 18%;
}

.sidebar__avatar {
  margin-bottom: 10px;
  border: 3px solid white;
  padding: 0 !important;
}

.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border: 1px solid lightgray; */
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}

.sidebar__top > h4 {
  color: gray;
  font-size: 12px;
  font-weight: 400;
}

.sidebar__top > h2 {
  font-size: 16px;
}

.sidebar__top > img {
  margin-bottom: -30px;
  width: 100%;
  /* height: 150px; */
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}

.sidebar__stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.sidebar__stat > p {
  font-size: 13px;
  color: gray;
  font-weight: 600;
}

.stat__number {
  font-weight: bold;
  color: #0a66c2 !important;
}

.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: 1px solid lightgray;
  background-color: white;
}

.sidebar__buttom {
  text-align: left;
  padding: 10px;
  /* border: 1px solid lightgray; */
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}

.sidebar__buttom > p {
  font-size: 13px;
  padding-bottom: 10px;
  font-weight: 600;
}
.sidebar__recentItems {
  display: flex;
  font-size: 13px;
  color: grey;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 5px;
  padding: 5px;
}

.sidebar__recentItems:hover {
  color: black;
  border-radius: 10px;
  cursor: pointer;
  background-color: whitesmoke;
}

.sidebar__hash {
  margin-right: 5px;
  margin-left: 5px;
}

@media (min-width: 320px) and (max-width: 768px) {
  .side__bar {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .side__bar {
    display: none;
  }
}
