.headerOption__title {
  font-size: 14px;
  font-weight: 400;
}

.headerOption__icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}

.header__options {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  /* margin-left: 35px; */
  margin: 0 15px;
  color: gray;
  cursor: pointer;
}

.header__options:hover {
  color: black;
}

.vertical__line {
  border-left: 0.1px solid lightgray;
  margin-right: 25px;
}
