.feed__area {
  flex: 0.45;
  margin: 0 20px;
}

.feed__inputWrapper {
  justify-content: space-evenly;
  padding: 10px;
  padding-bottom: 10px;
  border-radius: 10px;
  margin-bottom: 20px;
  background-color: white;
}

.feed__input {
  border: 1px solid lightgray;
  border-radius: 30px;
  display: flex;
  padding: 10px;
  color: gray;
  padding-left: 15px;
  margin-bottom: 10px;
}

.feed__input > form {
  display: flex;
  width: 100%;
}

.feed__input > form > input {
  outline: none;
  border: 0px;
  flex: 1;
  margin-left: 10px;
}

.feed__input > form > button {
  display: none;
}

.feed_inputOptions {
  display: flex;
  justify-content: space-evenly;
}

.horizontal__line {
  border-bottom: 1px solid lightgray;
  margin-bottom: 20px;
}
