.widget__area {
  flex: 0.23;
  top: 80px;
  position: sticky;
  height: fit-content;
  min-width: 25%;
}

.widget__feedheader {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  /* border: 1px solid lightgray; */
  border-bottom: 0;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;
  padding: 15px 15px;
  align-items: center;
}

.widget__feedbody {
  padding: 10px 15px;
  display: flex;
  flex-direction: column;
  justify-content: left;

  background-color: white;
  /* border: 1px solid lightgray; */
  border-top: 0;
  border-bottom: 0;
}

.widget__areafooter {
  display: flex;
  justify-content: left;

  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: white;
  /* border: 1px solid lightgray; */
  border-top: 0;
  padding: 10px 15px;
  padding-bottom: 15px;
  cursor: pointer;
}

@media (min-width: 320px) and (max-width: 768px) {
  .widget__area {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .widget__area {
    display: none;
  }
}
