.header {
  position: sticky;
  top: 0;
  z-index: 999;
  display: flex;
  justify-content: space-evenly;
  align-self: center;
  border-bottom: 0.1px solid lightgrey;
  padding: 7px 0px;
  width: 100%;
  background-color: white;
  margin-bottom: 25px;
}

.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}

.header__left {
  display: flex;
  flex: 0.3;
}

.header__search {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  background-color: #eef3f8;
  height: 21px;
  color: gray;
  width: 100%;
}

.header__search > input {
  outline: none;
  border: none;
  background: none;
  width: 100%;
}

.header__right {
  display: flex;
  justify-content: space-between;
}

.vertical__line {
  margin-left: 10px;
}
