.widget__profiledetails {
  display: flex;
}

.profile__follow {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-bottom: 10px;
}

.follow__button {
  display: flex;
}

.profile__follow > h4 {
  font-size: clamp(8px, 10vw, 14px);
  cursor: pointer;
}

.profile__follow > p {
  font-size: clamp(8px, 10vw, 13px);
  color: gray;
  font-weight: 400;
}

.follow__button {
  border: 1px solid lightgray;
  border-radius: 25px;
  padding: 3px 10px;

  margin-top: 5px;
  max-width: fit-content;
  cursor: pointer;
}

.follow__button:hover {
  background-color: whitesmoke;
  border: 1px solid lightgray;
}

.follow__button > h3 {
  font-size: clamp(8px, 8vw, 15px);
}
